

*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    color: #fff;
}

body {
    background: #000;
}

h3 {
    font-family: 'Teko', sans-serif;
    font-size: 30px;
}
p {
    font-family: 'Teko', sans-serif;
    letter-spacing: 10px;
}
.nav_bar {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo{
    margin: 10px;
    font-size: 20px;
    color: #fff;
}
.logo h3 {

    font-family: 'Smooch', cursive;
}
 i{
    font-family: 'Smooch', cursive;
    color: #3ab7ff;
}

.nav {
    display: flex;
}
.nav li {
    margin: 10px;
}
.nav li a{
    font-size: 15px;
    cursor: pointer;
    font-family: 'Teko', sans-serif;
}


img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.load_box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
}

.load_gap{
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.load_bar {
    width: 5px;
    height: 30px;
    border-radius: 10px;
    background-color: #3aa7ff;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: all ease-in-out 0.5s;
    -webkit-transition: all ease-in-out 0.5s;
    -moz-transition: all ease-in-out 0.5s;
    -ms-transition: all ease-in-out 0.5s;
    -o-transition: all ease-in-out 0.5s;
}
.lb1{
    animation: pop infinite 0.2s;
    -webkit-animation: pop infinite 0.2s;
}
.lb2{
    animation: pap infinite 0.2s;
    -webkit-animation: pap infinite 0.2s;
}
.lb3{
    animation: pep infinite 0.2s;
    -webkit-animation: pep infinite 0.2s;
}

@keyframes pop {
    0%{height: 25px;}
    50%{height: 70px;}
}
@keyframes pap {
    0%{height: 75px;}
    50%{height: 10px;}
}
@keyframes pep {
    0%{height: 105px;}
    50%{height: 30px;}
}

.Image_Card {
    width: 90%;
    height: auto;
    margin: 10px 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.Card {
    width: 450px;
    height: auto;
    margin: 10px;
}

.CardBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


.CardText {
    width: 100%;
    height: auto;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.text {
    display: flex;
}

.span {
    padding: 6px 8px;
    background: #067bdb;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    margin: 3px;
    font-family: 'Oswald', sans-serif;
    color: #000;
}

.searched{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.search_box {
    width: 90%;
    height: auto;
    margin: 10px 5%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search {
    width: auto;
    height: auto;
}
.search input[type='text']{
    width: 300px;
    height: 40px;
    padding: 0px 10px;
    background: transparent;
    border: 2px solid #3ab7ff;
    font-size: 23px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    margin: 0 5px;
    font-family: Teko, sans-serif;
}

.search input[type='submit']{
    padding: 6px 20px;
    border: none;
    outline: none;
    background: #3aa7ff;
    color: #000;
    font-size: 18px;
    cursor: pointer;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: 'Teko', sans-serif;
    font-size: 20px;
}
.search input[type='submit']:hover {
    border: 2px solid #fff;
    background: none;
    color: #3aa7ff;
}


@media  only screen and (min-width: 250px) and (max-width: 480px) {
    h3 {
        font-family: sans-serif;
        font-size: 20px;
    }
    
    .text {
        display: flex;
        flex-direction: column;
    }
    .span {
        padding: 6px 8px;
        background: #3aa7ff;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        margin: 3px;
        font-family: 'Oswald', sans-serif;
    }
    .spans {
        padding: 6px 8px;
        background: #000;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        margin: 3px;
        font-family: 'Oswald', sans-serif;
        color: #fff;
    }

    .search input[type='text']{
        width: 90%;
        margin: 05px 0%;
        height: 40px;
        padding: 0px 10px;
        background: transparent;
        border: 2px solid #3ab7ff;
        font-size: 23px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        font-family: Teko, sans-serif;
    }
    .CardText {
        width: 100%;
        height: auto;
        padding-bottom: 10px;
        display: block;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
}